import React from 'react';
import CardContainer from './CardContainer';

const formStyle = {
    width: '50%',
    margin: 'auto'
}

const inputStyle = {
    width: '20%',
    display: 'block',
    padding: '10px',
    textAlign: 'center',
    border: '2px solid black',
    borderRadius: '20px',
    margin: '30px auto 0 auto',
}

class PreferenceForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            name: '',
            ranking: ['Cost','Proximity to campus','Proximity to each other','Safety','Room space','Overall living space'],
        };
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.reorderRanking = this.reorderRanking.bind(this);
    }

    handleInputChange(event) {
        this.setState({name: event.target.value});
    }

    handleSubmit(event) {
        console.log(this.state);
        event.preventDefault();
    }

    reorderRanking(newRanking) {
        this.setState({ranking: newRanking});
    }

    render() {
        return (
            <form style = {formStyle} onSubmit = {this.handleSubmit}>
                <input placeholder = 'Name' style = {inputStyle} type = 'text' value = {this.state.name} onChange = {this.handleInputChange}/>
                <CardContainer cardMessages = {this.state.ranking} reorderRanking = {this.reorderRanking}/>
                <input style = {inputStyle} type = 'submit' value = 'Submit'/>
            </form>
        )
    }
}

export default PreferenceForm;





